
import { defineComponent } from "vue";
import TypedComponent from "@/components/Typed.vue";

export default defineComponent({
	name: "Header",
	components: {
		TypedComponent,
	},
	methods: {
		mobileNav() {
			let x = document.getElementById("topNav");
			if (x !== null) {
				if (x.className === "topnav") {
					x.className += " responsive";
				} else {
					x.className = "topnav";
				}
			}
		},
	},
});
